import React from 'react'
import { List, ListItem, Typography } from '@material-ui/core'

import '../styles/more-info.scss';

export default function moreInfo({ why, method, accuracy, version }) {
  return (
    <div id="MoreInfo">
      <h4 className="rainbow-text">Behind The Scene</h4>
      <List>
        <ListItem>
          <List>
            <Typography variant="h5">Why?</Typography>
            <ListItem>
              <div className="content" dangerouslySetInnerHTML={{ __html: why }}></div>
            </ListItem>
          </List>
        </ListItem>
        <ListItem>
          <List>
            <Typography variant="h5">Method?</Typography>
            <ListItem>
              <div className="content" dangerouslySetInnerHTML={{ __html: method }}></div>
            </ListItem>
          </List>
        </ListItem>
        <ListItem>
          <List>
            <Typography variant="h5">Accuracy?</Typography>
            <ListItem>
              <div className="content" dangerouslySetInnerHTML={{ __html: accuracy }}></div>
            </ListItem>
          </List>
        </ListItem>
        <ListItem>
          <List>
            <Typography variant="h5">Version?</Typography>
            <ListItem>
              <div className="content" dangerouslySetInnerHTML={{ __html: version }}></div>
            </ListItem>
          </List>
        </ListItem>
      </List>
    </div>
  )
}
